.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.mbody {
  min-height: 100vh;
}

.mmenu {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  /* required */
}

@media only screen and (max-width: 959px) {
  .mbody {
    padding-left: 0%;
    margin-top: 100vh;
    z-index: 10;
    position: relative;
  }

}


@media only screen and (min-width: 960px) {
  .mbody {
    padding-left: 30%;
  }
}

.profilePic {
  border: 8px solid #c2c6ce;
}

.bg-gold2 {
  background: #E3872D;
}

.bg-navy2 {
  background: #041230;
}

.form-control:focus {
  outline: none;
  border: 2px solid #E3872D;
}

.form-control {
  border: 2px solid rgba(0, 0, 0, 0.15);
  border-radius: 2%;
}

.sidebar::after {
  content: "";
  background: url(assets/download.png);
  opacity: 0.12;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: 1;
}

.titleTxt {
  font-weight: 500;
  text-shadow: 2px 8px 6px rgba(0, 0, 0, 0.2),
    0px -5px 35px rgba(255, 255, 255, 0.3);
}


.timeline-item {
  border-left: 2px solid #E3872D;
}

.timeline-item::last-child {
  -webkit-border-image: -webkit-gradient(linear,
      left top, left bottom,
      color-stop(60%, rgba(0, 0, 0, 0.2)),
      to(rgba(rgba(0, 0, 0, 0.2), 0))) 1 100%;
  -webkit-border-image: -webkit-linear-gradient(top,
      rgba(0, 0, 0, 0.2) 60%,
      rgba(rgba(0, 0, 0, 0.2), 0)) 1 100%;
       -o-border-image: -o-linear-gradient(top,
      rgba(0, 0, 0, 0.2) 60%,
      rgba(rgba(0, 0, 0, 0.2), 0)) 1 100%;
          border-image: -webkit-gradient(linear,
      left top, left bottom,
      color-stop(60%, rgba(0, 0, 0, 0.2)),
      to(rgba(rgba(0, 0, 0, 0.2), 0))) 1 100%;
          border-image: linear-gradient(to bottom,
      rgba(0, 0, 0, 0.2) 60%,
      rgba(rgba(0, 0, 0, 0.2), 0)) 1 100%;
}

