.timeline-item::after {
  width: 10px;
  height: 10px;
  display: block;
  top: 1em;
  position: absolute;
  left: -7px;
  border-radius: 10px;
  content: '';
  border: 2px solid #E3872D;
  background: white;
}

.timeline-item  .timeline-body {
  margin-top: -1rem
}
.timeline-item .timeline-date {
  margin-left: -0.8rem
}
.timeline-item .timeline-date::before {
  content: '';
  position: absolute;
  width: 1.2rem;
  height: 2px;
  background: #E3872D;
  top: 1.3rem;
  left: 0;
}


